.nav {
  display: flex;
  box-sizing: border-box;
  margin-bottom: 72px;

  &--no-js {
    margin-bottom: 160px;
  }
}

.nav__cities {
  display: none;
}

.nav__list {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  list-style: none;
}

.nav__item {
  box-sizing: border-box;
}

.nav__link {
  box-sizing: border-box;

  text-decoration: none;
}

.nav__join-button {
  display: block;
  box-sizing: border-box;
  padding: 0;

  text-transform: none;

  background: none;
  border: none;

  @include regular(14px, 1.44em, $white);
}

@media (max-width: $only-portable) {
  @keyframes bubble {
    from {
      transform: scale(1);
    }

    to {
      transform: scale(12);
    }
  }

  @keyframes opacify {
    from {
      color: rgba($blue, 0);
    }

    to {
      color: rgba($blue, 1);
    }
  }

  .nav__list {
    position: absolute;
    top: 10px;
    right: 2px;
    z-index: 0;

    display: none;
    width: 184px;
    padding: 20px 35px 20px 10px;
    overflow: hidden;

    color: $blue;

    border-radius: 10px;

    &--open {
      animation: opacify;
      animation-duration: 0.15s;
      animation-delay: 0.15s;

      animation-fill-mode: both;
    }

    &--close {
      animation: opacify;
      animation-duration: 0.15s;
      animation-direction: reverse;

      animation-fill-mode: both;
    }

    &--visible {
      display: block;
    }

    &--no-js {
      display: block;

      color: $white;
    }

    &::before {
      content: "";
      position: absolute;
      top: 6px;
      right: 0;
      z-index: -1;

      display: none;
      box-sizing: border-box;
      width: 42px;
      height: 42px;

      background-color: $white;
      border-radius: 50%;
      transform: scale(12);
    }

    &--visible::before {
      display: block;
    }

    &--open::before {
      animation-name: bubble;
      animation-duration: 0.3s;

      animation-fill-mode: both;
    }

    &--close::before {
      animation-name: bubble;
      animation-duration: 0.3s;
      animation-direction: reverse;

      animation-fill-mode: both;
    }
  }

  .nav__link {
    @include regular(14px, 1.44em, inherit);

    &:hover {
      font-family: "montserrat-bold";
    }
  }

  .nav__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  .nav__menu-button {
    position: absolute;
    top: 22px;
    right: 8 / 320 * 100%;

    box-sizing: border-box;
    width: 30px;
    height: 30px;

    background: none;
    background-image: url("../img/nav/icon-menu.svg");
    background-repeat: no-repeat;
    background-position: center;
    border: none;

    &--close {
      background-image: url("../img/nav/icon-menu-close.svg");
    }

    &--no-js {
      background: none;
    }
  }

  @media (min-width: $tablet-width) {
    .nav__list {
      top: 20px;
      right: 4px;
    }

    .nav__menu-button {
      top: 33px;
      right: 16px;
    }
  }
}

@media (min-width: $tablet-width) {
  .nav {
    align-items: flex-start;
    margin-bottom: 14px;

    &--no-js {
      margin-bottom: 54px;
    }
  }

  @media (max-width: $only-portable) {
    .nav__cities {
      position: relative;

      display: block;
      box-sizing: border-box;
      min-width: 166px;
      margin: 0;
      margin-right: 112px;
      padding: 0;
      padding-top: 36px;

      list-style: none;
      color: rgba(0, 0, 0, 0);

      visibility: hidden;

      transition: 0.2s ease-in-out;

      &--visible {
        color: $blue;

        visibility: visible;
      }

      &--no-js {
        color: $blue;

        visibility: visible;
      }
    }

    .nav__city {
      box-sizing: border-box;
      margin-bottom: 20px;

      @include regular(14px, 1.2em, inherit);

      &--current {
        position: absolute;
        top: 0;
        left: 0;

        background-image: url("../img/nav/nav-vector.svg");
        background-repeat: no-repeat;
        background-position: right center;
        visibility: visible;

        @include bold(14px, 1.2em, $blue);
      }
    }

    .nav__city-link {
      display: block;
      box-sizing: border-box;
      padding-right: 22px;

      font: inherit;
      color: inherit;
      text-transform: uppercase;
      text-decoration: none;
    }
  }
}
